.block-task-container {
	width: 90%;
	margin: 0px auto;
	-webkit-box-shadow: 9px 15px 13px 0px rgba(0, 0, 0, 0.14), inset 0px 0px 40px 0px rgba(255, 255, 255, 0.05);
	box-shadow:         9px 15px 13px 0px rgba(0, 0, 0, 0.14), inset 0px 0px 40px 0px rgba(255, 255, 255, 0.05);
	border-radius: 1rem;
	text-align: center;
	padding-bottom: 20px;
}
.block-task-container a {
	color: #bfe1ff;
}
.task-button-back {
	text-align: right;
	padding: 0.5rem;
}
.task-button-back a{
	background: #45E1BD;
	padding: 0.25rem 0.5rem;
	border-radius: 0.25rem;
	color: #3D4149;
	font-weight: bold;
	font-size: 14px;
}
.block-Dropdown-wrap {
	width: 60%;
	margin: 0 auto;
	display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-line-pack: start;
    align-content: flex-start;
    -ms-flex-align: start;
    align-items: flex-start;
}
.block-Dropdown {

}
.block-Dropdown .dropdown {
	margin-bottom: 5px;
	text-align: center;
}
.block-Dropdown button{
	background-color: #3F424A;
	color: #FFFFFF;
	border: none;
	width: 120px;
	font-size: 16px;
	-webkit-box-shadow: 2px 3px 10px 0px rgba(0, 0, 0, 0.25), inset 0px 0px 10px 0px rgba(255, 255, 255, 0.25);
	box-shadow:         2px 3px 10px 0px rgba(0, 0, 0, 0.25), inset 0px 0px 10px 0px rgba(255, 255, 255, 0.25);
}
.block-Dropdown button:hover{
	background-color: #3F424A;
}
.block-Dropdown button:active{
	background-color: #3F424A;
    border: none!important;
}
.block-Dropdown .dropdown-menu {
	background: #000000;
}
.block-Dropdown .dropdown-menu button{
	background: #000000;
	-webkit-box-shadow: none;
	box-shadow: none;
}
.block-Dropdown .dropdown-menu button:hover{
	color: #FFFFFF;
	text-decoration: underline;
}
.block-Dropdown .dropdown-menu {
	top: 30px;
	left: 50%;
	-webkit-transform: translate(-50%,0%);
	-ms-transform: translate(-50%,0%);
	transform: translate(-50%,0%);
}
.block-Dropdown span {
	display: inline-block;
	color: #FFFFFF;
	width: 120px;
	min-height: 28px;
	font-size: 14px;
	background: #2C3037;
	padding: 0.25rem 0.5rem;
	margin-top: 0.25rem;
	border-radius: 0.25rem;
	-webkit-box-shadow: inset 1px 1px 3px 0px rgba(0, 0, 0, 0.4), 1px 1px 1px 0px rgba(255, 255, 255, 0.5);
	box-shadow:         inset 1px 1px 3px 0px rgba(0, 0, 0, 0.4), 1px 1px 1px 0px rgba(255, 255, 255, 0.5);
}
.name-field {
	width: 60%;
	min-height: 28px;
	margin: 5px auto;
	background: #2C3037;
	border-radius: 0.25rem;
	text-align: left;
	color: #FFFFFF;
	padding: 0.25rem 0.5rem;
	-webkit-box-shadow: inset 1px 1px 3px 0px rgba(0, 0, 0, 0.4), 1px 1px 1px 0px rgba(255, 255, 255, 0.5);
	box-shadow:         inset 1px 1px 3px 0px rgba(0, 0, 0, 0.4), 1px 1px 1px 0px rgba(255, 255, 255, 0.5);
}
.name-field pre{
	color: #FFFFFF;
	white-space: pre-wrap;
	text-align: justify;
	margin-bottom: 0rem;
}
.dropzone {
	margin-top: 10px;
	position: relative;
}
.dropzone div:first-child {
	display: -ms-flex;
	display: -ms-flexbox;
	display: flex;
	-ms-align-items: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: 0 auto;
	width: 60% !important;
}
