.attachedFile {
	margin: 5px 10px;
	display: inline-block;

}
.attachedFile span{
	display: inline-block;
	max-width: 165px;
	overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: middle;
}
.attachedFile img {
	display: block;
	margin: 5px auto;
	width: auto;
	height: 100px;
}
.attachedFile:hover {
	background: rgba(0, 0, 0, 0.1);
}
.attachedFile:hover img{
	-ms-transform: scaleX(-1);
	-webkit-transform: scaleX(-1);
	transform: scaleX(-1);
	-o-transition: all 0.5s ease-in-out;
	-webkit-transition: all 0.5s ease-in-out;
	transition: all 0.5s ease-in-out;
}
@media screen and (max-width: 992px) {
	.block-task-container {
		width: 100%;
	}
	.block-Dropdown-wrap {
		width: 100%;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
	}
	.name-field {
		width: 100%;
	}
	.block-Dropdown {
		margin-bottom: 10px;
	}
	.dropzone div:first-child {
		width: 100% !important;
	}
}



