.tasks {
}
.tasks thead th {
  color: #fff;
  border-bottom: 0px solid #eceeef;
  border-top: none;
  cursor: pointer;
}
.tasks td {
  border-top: 1px solid #4f535d;
  overflow: hidden;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
  white-space: nowrap;
}
.tasks td a, .tasks td a strong {
  color: #F86F71;
}
.tasks .tasks__title {
  max-width: 200px;
}
/*.tasks .tasks__edit {
  max-width: 180px;
}*/
.avatar {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}
.datepicker {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.datepicker > div:nth-child(-n+4) {
  display: inline;
  margin: 0 5px;
}
.react-datepicker-wrapper {
  width: 95px;
}
.day-button {
  padding-left: .3rem;
  padding-right: .3rem;
}

.is--active {
  color: #000;
}
.is--asc:before {
  position: absolute;
  margin-left: -16px;
  content: "▲";
}
.is--desc:before {
  position: absolute;
  margin-left: -16px;
  content: "▼";
}

.status--new {
  color: #868e96;
}
.status--verified {
  color: #5bc0de;
}
.status--on-complection {
  color: #ffc107;
}
.status--work {
  color: #007bff;
}
.status--done {
  color: #28a745;
}
.pagination {
  color: #000;

}
.pagination a {
  cursor: pointer;
}
@media screen and (max-width: 992px) {
  .search {
    margin-top: 5px;
  }
}
@media screen and (max-width: 480px) {
  .datepicker {
    display: none;
  }
}
@media screen and (max-width: 575px) {
  .caption {
    display: none;
  }
}



